.main {
  width: 100%;
  height: 100%;
}

h1 {
  font-family: p22-mackinac-pro, serif;
  font-size: 50px;
  font-weight: 500;
  color: #646464;
  line-height: 150%;
  margin-top: 0;
}

.projects a {
  text-decoration: none;
}

h5 {
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  text-align: left;
}

.header-color-span,
h5 {
  color: #000;
}

.home {
  display: block;
  display: flex;
  height: 100%;
  max-width: 100%;
}

.home-project {
  overflow: hidden;
  position: relative;
  min-height: 400px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.home.mi-sched {
  background-color: rgb(215, 233, 247);
}

.homes.image-inner {
  position: absolute;
  height: auto;
}

.rom-vid {
  margin: auto;
}

.rom-vid video {
  position: absolute;
  height: auto;
  bottom: -10%;
  left: 0;
  right: 0;
  margin: auto;
}

.home.wish .image-inner {
  bottom: -40%;
  width: 90%;
  left: 0;
  right: 0;
  margin: auto;
}

.home.mi-sched .image-inner {
  bottom: 0;
  width: 100%;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  left: 0;
  right: 0;
  margin: auto;
}

.home.potluck .image-inner {
  width: 550px;
  bottom: -20%;
  right: 0;
  left: 0;
  margin: auto;
}

.image-inner,
.home.movingchar .image-inner {
  left: 0;
  right: 0;
  margin: auto;
  width: 90%;
  bottom: -30%;
}

.home.movingchar .image-inner {
  height: auto;
}

.home.finary .image-inner {
  width: 100%;
  height: auto;
  bottom: 0%;
  left: 0;
  right: 0;
  margin: auto;
}

.home.bsc .image-inner {
  bottom: -30%;
}

.home.bsc .image-inner,
.home.rom .image-inner {
  width: 90%;
  height: auto;
  left: 0;
  right: 0;
  margin: auto;
}

.home.rom .image-inner {
  bottom: -10%;
}

.home.meta .image-inner {
  width: 50%;
  height: auto;
  left: 0;
  right: 0;
  bottom: 5%;
  margin: auto;
}

.home.menti .image-inner {
  width: 95%;
}

.home.align .image-inner,
.home.menti .image-inner {
  height: auto;
  left: 0;
  right: 0;
  bottom: -20%;
  margin: auto;
}

.home.align .image-inner {
  width: 90%;
}

.home.wish-dc .image-inner {
  width: 100%;
  height: auto;
  bottom: -80%;
  left: 0;
  right: 0;
  margin: auto;
}

.image-inner {
  width: 90%;
  height: auto;
  bottom: -30%;
  left: 0;
  right: 0;
  margin: auto;
}

.home.bsc {
  background-color: #091331;
}

.home.movingchar {
  background-color: #0b2840;
}

.home.finary {
  background-color: rgb(211, 228, 205);
}

.home.rom {
  background-color: #ad4219;
}

.home.meta {
  background: linear-gradient(
    rgb(217, 215, 241) 22.5%,
    rgba(217, 215, 241, 0) 100%
  );
}

.home.menti {
  background-color: #223230;
}

.home.potluck {
  background-color: #362c8c;
}

.home.wish {
  background-color: #1a6057;
}

.home.wish-dc {
  background-color: #004e6b;
}

.home-project:hover {
  text-decoration: none;
  -webkit-transform: scale(1.02);
  transform: scale(1.02);
  z-index: 99;
  box-shadow: 5px 17px 40px -11px rgba(20, 30, 28, 0.3333333333333333);
}

.home.align .para,
.homes.align .internships,
.internships,
.para,
b {
  color: #000;
}

.homes.para {
  color: #000;
  font-family: manrope, sans-serif;
}

.homes.info {
  padding: 32px;
  flex-direction: column;
  display: flex;
  width: 100%;
}

.homes.image {
  padding: 32px 0;
  overflow: hidden;
  align-items: center;
  display: flex;
}

.homes.internships {
  color: #000;
  font-family: p22-mackinac-pro, serif;
  font-size: 28px;
  line-height: 120%;
  letter-spacing: 0;
}

.homes.internships,
h6 {
  font-style: normal;
  font-weight: 600;
  text-align: left;
}

h6 {
  font-family: manrope;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.textloop {
  position: absolute;
}

.home.projects {
  display: grid;
  flex-wrap: wrap;
  max-width: 1240px;
  margin: 32px auto 85px;
  transition: all 0.3s ease;
  height: 100%;
  padding: 0 20px;
  grid-template-columns: repeat(2, 1fr);
}

.App-header {
  display: flex;
}

.header-about,
.header-welcome {
  font-family: manrope, sans-serif;
}

.header-about {
  flex: 1 1;
  padding-top: 48px;
  padding-bottom: 48px;
  padding-right: 48px;
  border-right: 1px solid #d6d6db;
}

.header-welcome {
  padding-top: 48px;
  flex: 1 1;
  padding-bottom: 48px;
  padding-left: 48px;
}

.header-welcome p {
  font-family: Manrope;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0;
  text-align: left;
  color: #000;
}

.section-header {
  text-transform: capitalize;
}

.section-header p {
  margin: 0;
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  text-align: left;
  color: #000;
}

.header-weight-span {
  font-style: italic;
}

@media screen and (max-width: 1600px) {
  h1 {
    font-size: 50px;
  }
}

@media screen and (max-width: 991px) {
  .home.movingchar .image-inner {
    bottom: -30%;
    width: 50%;
  }

  .rom-vid video {
    bottom: -20%;
    width: 70%;
  }

  .home.finary .image-inner {
    bottom: 0%;
    width: 100%;
  }

  .home.meta .image-inner,
  .home.rom .image-inner {
    bottom: 0%;
    width: 50%;
  }

  .home.align .image-inner {
    bottom: -30%;
    width: 50%;
  }

  .home.projects {
    grid-gap: 20px;
    grid-gap: 20px;
    gap: 20px;
  }

  .home.mi-sched .image-inner {
    bottom: 0;
    width: 100%;
  }

  .home.wish .image-inner {
    bottom: -30%;
    width: 50%;
  }

  .image-inner {
    bottom: -30%;
    width: 60%;
  }

  .home.wish-dc .image-inner {
    bottom: -70%;
    width: 60%;
  }

  .home {
    grid-column: auto/span 2;
  }

  .header-welcome h5 {
    display: none;
  }

  .App-header {
    max-width: 600px;
  }

  .textloop {
    margin: auto;
    left: 0;
    right: 0;
  }

  h1 {
    font-size: 50px;
    text-align: center;
  }

  .App-header {
    flex-direction: column;
  }

  .header-welcome {
    padding-left: 0;
    margin-top: 68px;
  }

  .header-welcome p {
    text-align: center;
  }

  .header-about {
    padding-right: 0;
    border-right: none;
  }

  .textloop {
    width: 100%;
  }

  .home.bsc .image-inner {
    width: 70%;
    bottom: -50%;
  }
}

@media screen and (max-width: 870px) {
  .rom-vid video {
    bottom: -20%;
    width: 90%;
  }

  .home.bsc .image-inner {
    width: 80%;
    bottom: -40%;
  }

  .home.movingchar .image-inner {
    bottom: -30%;
    width: 70%;
  }

  .home.finary .image-inner {
    bottom: 0%;
    width: 100%;
  }

  .home.meta .image-inner,
  .home.rom .image-inner {
    bottom: 0%;
    width: 50%;
  }

  .home.align .image-inner {
    bottom: 0;
    width: 70%;
  }

  .home.wish .image-inner {
    bottom: -30%;
    width: 70%;
  }

  .home.mi-sched .image-inner {
    bottom: 0;
    width: 100%;
  }

  .image-inner {
    bottom: -30%;
    width: 70%;
  }

  .home.wish-dc .image-inner {
    bottom: -70%;
    width: 70%;
  }
}

@media screen and (max-width: 650px) {
  .home.bsc .image-inner {
    width: 80%;
    bottom: -10%;
  }

  .home.movingchar .image-inner {
    bottom: -20%;
    width: 80%;
  }

  .rom-vid video {
    bottom: 0;
    width: 90%;
  }

  .home.finary .image-inner {
    bottom: 0%;
    width: 100%;
  }

  .home.mi-sched .image-inner,
  .home.rom .image-inner {
    bottom: 0;
    width: 100%;
  }

  .home.wish .image-inner {
    bottom: -35%;
    width: 80%;
  }

  .image-inner {
    bottom: -20%;
    width: 80%;
  }

  .home.wish-dc .image-inner {
    bottom: -50%;
    width: 80%;
  }
}

@media screen and (max-width: 575px) {
  .header-about h1 {
    font-size: 40px;
  }

  .header-welcome {
    padding-left: 0;
    margin-top: 48px;
  }

  .header-welcome p {
    font-size: 18px;
    line-height: 150%;
    max-width: 400px;
    margin: auto;
  }
}

@media screen and (max-width: 471px) {
  .homes.image-inner {
    margin-left: auto;
    height: 100%;
    float: right;
    width: unset;
  }

  .rom-vid video {
    bottom: -10%;
    width: 90%;
  }

  .home.movingchar .image-inner {
    bottom: -20%;
    width: 80%;
  }

  .home.potluck .image-inner,
  .home.rom .image-inner {
    bottom: -30%;
    width: 80%;
  }

  .home.mi-sched .image-inner {
    bottom: -40%;
    width: 150%;
    left: -25%;
  }

  .home.wish .image-inner {
    bottom: -45%;
    width: 80%;
  }

  .image-inner {
    bottom: -40%;
    width: 80%;
  }

  .home.wish-dc .image-inner {
    bottom: -50%;
    width: 80%;
  }

  .homes.image {
    padding: 0;
  }

  .homes.info {
    padding-bottom: 0;
  }

  .homes.date {
    padding-top: 20px;
  }

  .home.projects {
    grid-gap: 20px;
    grid-gap: 20px;
    gap: 20px;
  }

  .home {
    min-height: 300px;
  }
}

@media screen and (max-width: 426px) {
  .App-header h1 {
    font-size: 35px;
  }

  .header-welcome {
    margin-top: 35px;
  }
}

@media screen and (max-width: 396px) {
  .App-header h1 {
    font-size: 30px;
  }

  .header-welcome {
    margin-top: 20px;
  }
}

.header-welcome h5:first-child {
  margin-bottom: 24px;
}

.header-welcome h5:nth-child(2) {
  margin-top: 48px;
}

.header-serif {
  font-style: italic;
  line-height: 130%;
  color: #3d3737;
}

